
export class ExecuteBlock{
    constructor(applicator,tactics,executeStage,execute,duration,weight){
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.executeStage = executeStage;
        //
        this.execute = execute;
        //
        this.duration = duration;
        //
        this.beExecuted = null;
        //
        this.weight = weight == undefined ? 1 : weight
    }
    Init(target,prior = false){
        this.beExecuted = target;
        prior ? target.EXECUTE.unshift(this) : target.EXECUTE.push(this);
        return true;
    }
}


//
export class ExecuteSysBlock{
    constructor(applicator,tactics,executeStage,execute,duration){
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.executeStage = executeStage;
        //
        this.execute = execute;
        //
        this.duration = duration;
        //
        this.beExecuted = null;
    }
    Init(target,prior = false){
        this.beExecuted = target;
        prior ? target.driver.EXECUTE.unshift(this) : target.driver.EXECUTE.push(this);
        return true;
    }
    Executed(){
        this.execute();
    }
}
