import Report from "../js/report";

class IgnoreBlock{
    /**
     * 
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} ignoreAttrType 
     * @param {*} value 
     * @param {*} duration 
     * @param {*} waitTime 
     * @param {*} workTactics 
     */
    constructor(applicator,tactics,ignoreAttrType,value,duration,waitTime,workTactics){
        this.applicator = applicator;
        this.tactics = tactics;
        this.ignoreAttrType = ignoreAttrType;
        this.value = value;
        this.duration = duration;
        this.waitTime = waitTime;
        this.workTactics = workTactics == undefined ? null : workTactics;
        this.beExecuted = null;
    }
    Init(target,needReport = true,reportLevel = 3,reportStyle = 1){
        this.beExecuted = target;
        //
        for(let i in target.IGNORE){
            if(target.IGNORE[i].tactics.type == this.tactics.type && target.IGNORE[i].ignoreAttrType == this.ignoreAttrType){
                let r = new Report();
                r.InitExecuteReport(3,target,`${this.ignoreAttrType}`);
                return false;
            }
        }
        target.IGNORE.push(this);
        if(needReport){
            this.InitReport(reportStyle,reportLevel);
        }
        return true;
    }
    /**
     * 
     * @param {*} reportStyle 1:Ax%x 2:AxBx%x 3:Ax%x 4:Ax%x 5:AxBx%x
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `${this.value}%${this.ignoreAttrType}`;
        if(this.beExecuted.driver.turn < 1) msg += "";

        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`${this.tactics.name}`,msg);
        if(reportStyle == 3) {
            let r = new Report("");
            r.InitExecuteReport(reportLevel,this.beExecuted,msg+"");
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,msg+"");
        if(reportStyle == 5 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`${newTact.name}`,msg+"");
    }
}
export default IgnoreBlock;