
import { GetRange } from "./get";


const controlStatus = ["", "", "", ""];
const continueStatus = ["","","","",""]
const statusMap = {
    "": "STATUS", "": "STATUS", "": "STATUS", "": "STATUS","": "STATUS",
    "": "SPECIAL_STATUS","": "SPECIAL_STATUS","": "SPECIAL_STATUS","": "SPECIAL_STATUS","": "SPECIAL_STATUS",
}


/**
 * 
 * @param {object} target 
 * @returns 
 */
export const IsDodge = (target) => {
    return target.DODGE.some(block => {
        return (block.rate > 0);
    });
}

/**
 * tacticswhichStatus
 * @param {object} target 
 * @param {string} whichStatus 
 * @param {object} tactics 
 * @returns 
 */
export const IsHaveStatusByTactics = (target, whichStatus, tactics) => {
    if (!statusMap[whichStatus]) {
        return false;
    }
    return target[statusMap[whichStatus]].some(block => {
        return block.tactics == tactics; 
    });
}


/**
 * target
 * @param {*} target 
 * @returns 
 */
export const IsContinueStatus = (target)=> {
    return target.SPECIAL_STATUS.length > 0;
}

/**
 * target
 * @param {*} target 
 * @returns 
 */
export const IsControlled = (target)=> {
    for (let i in target.STATUS) {
        if (controlStatus.includes(target.STATUS[i].type) && target.STATUS[i].waitTime <= 0) {
            return true;
        }
    }
    return false;
}

/**
 * 
 * @param {object} target 
 * @param {object} whichTact 
 * @returns 
 */
export const IsControlledByTactics = (target, whichTact) => {
    for (let i in target.STATUS) {
        if (controlStatus.includes(target.STATUS[i].type) && target.STATUS[i].waitTime <= 0 && target.STATUS[i].tactics == whichTact) {
            return true;
        }
    }
    return false;
}

/**
 * 
 * @param {object} target 
 * @param {string} whichStatus 
 * @param {object} [tgtTact=null] whichStatus
 * @returns 
 */
export const IsGetStatus = (target, whichStatus,tgtTact=null) => {
    const res = target.STATUS.some(block=>{
        if(block.type == whichStatus && block.waitTime <= 0){
            //true
            if(tgtTact == null || tgtTact && block.tactics == tgtTact) return true;
        }
    })
    return res;
}

/**
 * 
 * @param {object} attacker 
 * @param {object} receiver 
 * @returns true
 */
export const IsCanAttackSomeone = (attacker, receiver) => {
    let dis = GetRange(attacker);
    for (let i = Number(attacker.position) + 1; i < 3; i++) {
        if (attacker.ally[i].alive) {
            dis -= 1;
        }
    }
    for (let i = 2; i >= 0; i--) {
        if (receiver.ally[i].alive && receiver.ally[i] != receiver) dis--;
        else if (receiver.ally[i] == receiver && dis > 0) return true;
    }
    return false;
}




/**
 * 
 * @param {object} target 
 * @returns 
 */
export const IsMaxTroopsInTeam = (target)=> {
    let highest = target.hp;
    for (let i in target.ally) {
        if (target.ally[i].hp >= highest && target.ally[i] != target) return false
    }
    return true;
}
/**
 * 
 * @param {object} target 
 * @returns 
 */
export const IsLowestTroopsInTeam = (target)=> {
    let lowest = target.hp;
    for (let i in target.ally) {
        if (target.ally[i].hp <= lowest && target.ally[i] != target) return false
    }
    return true;
}


/**
 * 
 * @param {*} target 
 * @param {*} tacticsType 
 * @param {*} statusType 
 * @returns 
 */
export const IsExistDamageStatus = (target, tacticsType, statusType)=> {
    for (let i in target.SPECIAL_STATUS) {
        if (tacticsType == '' && target.SPECIAL_STATUS[i].type == statusType) return true;
        if (target.SPECIAL_STATUS[i].type == statusType && target.SPECIAL_STATUS[i].tactics.type == tacticsType) return true;
    }
    return false;
}
/**
 * 
 * @param {*} target 
 * @param {*} tacticsType 
 * @param {*} damageType 
 * @param {*} isMakeDamage 
 * @param {*} isBenefit 
 * @returns 
 */
export const IsExistDamageBlock = (target, tacticsType, damageType, isMakeDamage, isBenefit)=> {
    for (let i in target.DAMAGE_VAR) {
        if (target.DAMAGE_VAR[i].tactics.type == tacticsType
            && target.DAMAGE_VAR[i].damageType == damageType
            && target.DAMAGE_VAR[i].isMakeDamage == isMakeDamage
            && target.DAMAGE_VAR[i].isBenefit == isBenefit) {
            return true;
        }
    }
    return false;
}


/**
 * x
 * @param {*} impactor 
 * @param {*} target 
 * @param {*} x 
 * @returns 
 */
export const IsInScale = (impactor, target, x)=> {
    let dis = x;
    for (let i = Number(impactor.position) + 1; i < 3; i++) {
        if (impactor.ally[i].alive) {
            dis -= 1;
        }
    }
    for (let i = 2; i >= 0; i--) {
        if (target.ally[i].alive && target.ally[i] != target) dis--;
        else if (target.ally[i] == target && dis > 0) return true;
    }
    return false;
}