import Report from "../js/report";
//
class RateBlock {
    constructor(applicator, tactics, workTactics, value, duration, waitTime, skipTime) {
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.workTactics = workTactics;
        //
        this.value = value;
        //
        this.duration = duration;
        //
        this.waitTime = waitTime;
        //
        this.skipTime = skipTime == undefined ? 0 : skipTime;
        //
        this.isBenefit = false;
        this.beExecuted = null;
        if (this.value > 0 || this.skipTime > 0) this.isBenefit = true;
    }
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        //
        const abort = target.RATE_VAR.some(block => {
            if (block.tactics.type == this.tactics.type && block.workTactics == this.workTactics) {
                let r = new Report();
                if (this.skipTime == 0) {
                    let txt = `${this.workTactics.name}${this.value > 0 ? "" : ""}`
                    r.InitExecuteReport(3, target, txt)
                    return true;
                }
                else if (this.skipTime > 0 && block.skipTime > 0
                    || this.skipTime < 0 && block.skipTime < 0) {
                    let txt = `${this.workTactics.name}${this.skipTime > 0 ? "" : ""}${Math.abs(this.skipTime)}`
                    r.InitExecuteReport(3, target, txt);
                    return true;
                }
            }
        })
        if (abort) return false;
        target.RATE_VAR.push(this);
        if (needReport) {
            this.InitReport(reportStyle,reportLevel);
        }
        return true;
    }
    /**
     * 
     * @param {*} reportStyle 1: Axx% 2: AxBxx% 3: Axx% 4: Axx% 5:AxBxx%
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null) {
        if(this.value){
            let r1 = new Report();
            let msg1 = `${this.workTactics.name}${this.isBenefit ? "" : ""}`;
            msg1 += this.value ? `${Math.abs(this.value)}%` : "";
            if(this.beExecuted.driver.turn < 1) msg1 += ""
            if (reportStyle == 1) r1.InitExecuteReport(reportLevel, this.beExecuted, msg1);
            if (reportStyle == 2) r1.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `${this.tactics.name}`, msg1);
            if(reportStyle == 3){
                r1 = new Report("");
                r1.InitExecuteReport(reportLevel, this.beExecuted, msg1 + "");
            }
            if (reportStyle == 4) r1.InitExecuteReport(reportLevel, this.beExecuted, msg1 + "");
            if (reportStyle == 5 && newApplicator && newTact) r1.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `${newTact.name}`, msg1+"");
        }

        if(this.skipTime){
            let r = new Report();
            let msg = `${this.workTactics.name}${this.isBenefit ? "" : ""}`;
            msg += this.skipTime ? `${Math.abs(this.skipTime)}` : ``;
            if(this.beExecuted.driver.turn < 1) msg += ""
            if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg);
            if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `${this.tactics.name}`, msg);
            if(reportStyle == 3){
                r = new Report("");
                r.InitExecuteReport(reportLevel, this.beExecuted, msg + "");
            }
            if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, msg + "");
            if (reportStyle == 5 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `${newTact.name}`, msg+"");
        }
    }
    SelfDestroy() {
        let temp = []
        for (let i in this.beExecuted.RATE_VAR) {
            if (this.beExecuted.RATE_VAR[i] != this) temp.push(this.beExecuted.RATE_VAR[i]);
        }
        this.beExecuted.RATE_VAR = temp;
    }
}

export default RateBlock;