/**
 * 
 * @param {*} target 
 */
export const ConsGetCureExecuteBlock = (target)=> {
    let remain = [];
    let len = target.GET_CURE.length;
    for (let i = 0; i < len; i++) {
        if (!target.GET_CURE[i]['action']) {
            target.GET_CURE[i]['action'] = true;
            if (target.GET_CURE[i].duration > 0) {
                remain.push(target.GET_CURE[i]);
            }
            target.GET_CURE[i].duration--;
        }
        else remain.push(target.GET_CURE[i]);
        if (target.GET_CURE.length != len) {
            i = 0;
            len = target.GET_CURE.length;
        }
    }
    //action
    for (let i in target.GET_CURE) {
        target.GET_CURE[i]['action'] = false;
    }
    target.GET_CURE = remain;
}


/**
 * stage
 * @param {*} target 
 * @param {string} stage ""....
 */
export const ConsExecuteBlock = (target, stage)=> {
    let remain = [];
    let len = target.EXECUTE.length;
    for (let i = 0; i < len; i++) {
        if (target.EXECUTE[i].executeStage == stage && !target.EXECUTE[i]['action']) {
            target.EXECUTE[i]['action'] = true;
            if (target.EXECUTE[i].duration > 0) {
                target.EXECUTE[i].execute(target.EXECUTE[i]);
                remain.push(target.EXECUTE[i]);
            }
            target.EXECUTE[i].duration--;
        }
        else remain.push(target.EXECUTE[i]);
        if (target.EXECUTE.length != len) {
            i = 0;
            len = target.EXECUTE.length;
        }
    }
    //action
    for (let i in target.EXECUTE) {
        target.EXECUTE[i]['action'] = false;
    }
    target.EXECUTE = remain;
}

/**
 * stage
 * @param {*} target 
 * @param {string} stage ""....
 */
export const ConsSysExecuteBlock = (sys,stage)=> {
    let work = sys.EXECUTE.some(block=>{
        if(block.executeStage == stage && stage == "") return true;
    })
    if(work){
        let r = new Report();
        r.InitSysMsg(sys.global.ReportHeap, "", ``);
    }
    let remain = [];
    sys.EXECUTE.forEach(block=>{
        if(block.duration > 0){
            if(block.executeStage == stage) block.Executed();
            remain.push(block);
        } 
        else block.duration -= 1;
    })
    sys.EXECUTE = remain;
    
}















