/**
 * 
 * @param {object} target 
 * @returns true
 */
export const ConsDodgeBlock = (target)=> {
    //
    let heighestOddDodge = target.DODGE[0];
    let remain = [];
    let suc = false;
    target.DODGE.forEach(block => {
        if(block.rate > heighestOddDodge.rate) heighestOddDodge = block;
    });

    let realOdd = GetMoraleRate(target, heighestOddDodge.rate);
    let rate = Math.floor(Math.random() * 100);
    if (rate < realOdd) {
        let r = new Report();
        r.InitExecuteReport(3, target, ``);
        suc = true;
    }
    else {
        let r = new Report();
        r.InitExecuteReport(3, target, `()${realOdd}%`);
    }
    target.DODGE.forEach(block => {
        if(block != heighestOddDodge) remain.push(block)
    });
    target.DODGE = remain;
    return suc;
}

/**
 *  
 * @param {*} target 
 * @param {*} isMakeDamage 
 */
export const ConsOnceTacticsDamageBlock = (target, isMakeDamage)=> {
    let remain = [];
    target.DAMAGE_VAR.forEach(block=>{
        if (block.isOnceTactics && block.isMakeDamage == isMakeDamage) {
            block.InitReport(3,3);
        }
        else remain.push(block);
    })
    target.DAMAGE_VAR = remain;
}

/**
 * 
 * @param {*} target 
 * @param {*} damage 
 * @param {*} isComAtk 
 * @param {*} damageType 
 * @param {*} tacticsItem 
 * @param {*} isMakeDamage 
 * @param {*} reportStyle 
 */
export const ConsOnceDamageBlock = (target,dmgId,isComAtk, damageType, tacticsItem, isMakeDamage,reportStyle=1)=> {
    let count = [];
    let remain = [];
    target.DAMAGE_VAR.forEach(block => {
        let typeControl = false;
        if (damageType == block.damageType || block.damageType == '') {
            typeControl = true;
        }
        //
        else if (tacticsItem != null && tacticsItem.type == '' && block.damageType == '') typeControl = true;
        //
        else if (tacticsItem != null && tacticsItem.type == '' && block.damageType == '') typeControl = true;
        //
        else if (tacticsItem == null && isComAtk && block.damageType == '') typeControl = true;
        if (block.isOnceAttack && typeControl && block.isMakeDamage == isMakeDamage && !(tacticsItem && block.isOnceTactics)) {
            //
            let flag = false;
            for (let j in count) {
                if (count[j].tactics == block.tactics) {
                    flag = true; break;
                }
            }
            if (flag) remain.push(block);
            //
            else {
                if (block.isOnceAttack || block.isOnceTactics) count.push(block);
                // let r = new Report('');
                // let str1 = block.isMakeDamage ? "" : "";
                // let str2 = "";
                // if (block.damageType == '') str2 = "";
                // else if (block.damageType == '') str2 = "";
                // else if (block.damageType == '') str2 = "";
                // else if (block.damageType == '') str2 = "";
                // else if (block.damageType == '') str2 = "";
                // else if (block.damageType == '') str2 = "";
                // let str3 = "";
                // if (block.value < -666) str3 = "";
                // else if (block.value > 0) str3 = "";
                // else str3 = "";
                // let str4 = "";
                // if (block.value >= -666) str4 = `${Math.abs(block.value)}%`
                
                // if(reportStyle == 2){
                //     r.InitTwoTargetReport(3, target, `${block.tactics.name}${str1 + str2 + str3 + str4}`,false);
                // }
                // else r.InitExecuteReport(3, target, `${block.tactics.name}${str1 + str2 + str3 + str4}`,false);
                let damageInfo = {
                    dmgId : dmgId,
                    block:block,
                    report : null
                }
                target.reportWait.push(damageInfo);
            }
        }
        //
        else remain.push(block);
    });
    target.DAMAGE_VAR = remain;
}






