export const HandlerOnceDamageVarReport = (target,dmgId)=>{
    target.reportWait.forEach(item=>{
        if(item.dmgId == dmgId){
            // let r = new Report();
            // r.PutReportToHeap(target.global.ReportHeap, item.report);
            item.block.InitReport(3,3);
        }
    })
}

/**
 * targetwhichTactwhichType
 * @param {*} target 
 * @param {object} whichTact 
 * @param {string} whichType  or
 */
export const TriggerCureStatus = (target,whichTact,whichType)=>{
    target.CURE_STATUS.forEach(block=>{
        if(block.tactics == whichTact && block.type == whichType) block.Execute(target,true);
    })
}

/**
 * targetwhichTactwhichType
 * @param {*} target 
 * @param {object} whichTact 
 * @param {string} whichType  
 */
export const TriggerDmgStatus = (target,whichTact,whichType)=>{
    target.SPECIAL_STATUS.forEach(block=>{
        if(block.tactics == whichTact && block.type == whichType) block.Execute(target,true);
    })
}



/**
 * 
 * @param {*} target 
 * @param {*} tacticsItem 
 * @returns let res = {
        launch: ,
        wait: ,
    };
 */
export const HandlerWaitTactics = (target, tacticsItem)=> {
    let remain = [];
    let res = {
        launch: null,
        wait: null,
    };
    target.WaitTacticsArr.forEach(tact => {
        if (tacticsItem == tact.tactics) {
            tact.waitTime--;
            //
            if (tact.waitTime <= 0) {
                let r = new Report();
                r.InitExecuteReport(2, target, `${tact.tactics.name}!`);
                tact.tactics.execute(target);
                //res
                res.launch = tact.tactics;
            }
            else {
                res.wait = tact.tactics;
                remain.push(tact);
            }
        }
        else remain.push(tact);
    });
    target.WaitTacticsArr = remain;
    return res;
}





