import Report from "../js/report";

import { CountCure, CountDamage } from "../func/cacl";
import { IsGetStatus } from "../func/judger";
import { Cure, Hurt } from "../func/processor";
import { GetMoraleRate } from "../func/get";


const benefitStatus = ["","","","","","","","","","","","","",""]




export class StatusBlock {
    /**
     * 
     * @param {object} applicator 
     * @param {object} tactics 
     * @param {string} type 
     * @param {number} duration 
     * @param {number|0} waitTime 
     * @param {boolean|true} canRemove true
     */
    constructor(applicator, tactics, type, duration, waitTime, canRemove) {
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.type = type;
        //
        this.duration = duration;
        //
        this.isBenefit = benefitStatus.includes(type);
        //
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        //
        this.canRemove = canRemove == undefined ? true : canRemove;
        //
        this.beExecuted = null;
        //target
    }
    /**
     * 
     * @param {object} target 
     * @param {boolean|true} needReport 
     * @param {number|3} reportLevel 
     * @param {number|1} reportStyle 121 A ....  2AxB ....
     * @returns 
     */
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        if (IsGetStatus(target, `${this.type}`)) {
            let report = new Report();
            report.InitExecuteReport(3, target, `${this.type}`);
            return false;
        }
        //()
        if (target.driver.turn >= 1) {
            const resistWork = target.STATUS_RESIST.some(block => {
                if (block.resistType == this.type) {
                    let rand = Math.floor(Math.random() * 100);
                    let realOdd = GetMoraleRate(target, block.rate)
                    if (rand < realOdd) {
                        let r = new Report();
                        r.InitTwoTargetReport(3, block.applicator, target, `${block.tactics.name}`, `${this.type}`);
                        return true;
                    }
                    else {
                        let r = new Report();
                        r.InitExecuteReport(3, block.applicator, `${block.tactics.name}${realOdd}%`);
                    }
                }
            })
            if(resistWork) return false;
        }
        //
        target.BEFORE_FALL_INTO_STATUS.forEach(block => {
            block.GetData(this.applicator, this.tactics, this.type, this.duration, this.isBenefit, this.waitTime, this);
            block.Execute();
        })
        //
        const abort = target.STATUS.some(block => {
            //
            if (block.type == this.type && this.type != '' && this.type != '') {
                let report = new Report();
                report.InitExecuteReport(3, target, `${block.type}`);
                return true;
            }
        })
        if (abort) return false
        target.STATUS.push(this);
        if (needReport) {
            this.InitReport(reportStyle, reportLevel)
        }
        //
        target.FALL_INTO_STATUS.forEach(block => {
            block.GetData(this.applicator, this.tactics, this.type, this.duration, this.isBenefit, this.waitTime, this);
            block.Execute();
        })
        return true;
    }
    /**
     * 
     * @param {*} reportStyle 1 A...  2AxB ... 3: Ax... 4: Ax... 5: AxB ...    
     * @param {*} reportLevel 
     */
    InitReport(reportStyle = 1, reportLevel = 3,newApplicator = null, newTact = null) {
        let msg = ``;
        if(this.type == '' || this.type == '' || this.type == '' || this.type == ''){
            if(this.beExecuted.driver.turn > 0 && this.waitTime <= 0) msg = `${this.type}`;
            else msg = `${this.type}`
        }
        else if (this.type == "") msg = ``;
        else if (this.type == "") msg = ``;
        else if (this.type == "" || this.type == "") msg = `${this.type}`;
        else if (this.type == '' || this.type == '') msg = `${this.type}`
        else if (this.type == '') msg = ``
        else if (this.type == '' || this.type == '') msg = `${this.type}`;
        else msg = `${this.type}`;

        if (this.waitTime > 0 || this.beExecuted.driver.turn < 1) msg += "()"
        if(reportStyle == 1 && this.beExecuted.driver.turn < 1) msg += ""
        let r = new Report();
        if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg);
        if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `${this.tactics.name}`, msg);
        if (reportStyle == 3){
            let r = new Report("");
            r.InitExecuteReport(reportLevel, this.beExecuted, `${this.tactics.name}${this.type}`);
        }
        if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, `${this.tactics.name}${this.type}`);
        if (reportStyle == 5) r.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `${newTact.name}`, `${this.type}`);
    }
    SelfDestroy() {
        let remain = []
        for (let i in this.beExecuted.STATUS) {
            if (this.beExecuted.STATUS[i] != this) remain.push(this.beExecuted.STATUS[i]);
        }
        this.beExecuted.STATUS = remain;
    }
}


export class DamageStatusBlock {
    /**
     * 
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {string} type 
     * @param {number} dmgMult 
     * @param {number} damageValue 
     * @param {string} damageType 
     * @param {*} duration 
     * @param {*} waitTime 
     * @param {boolean|true} isExecute 
     * @param {boolean|true} canRemove true
     * @param {Function|null} callBack ()
     */
    constructor(dmgId, applicator, tactics, type, dmgMult, damageValue, damageType, duration, waitTime, isExecute, canRemove, callBack) {
        this.dmgId = dmgId;
        this.applicator = applicator;
        this.tactics = tactics;
        this.type = type;
        this.dmgMult = dmgMult;
        this.damageValue = damageValue
        this.damageType = damageType;
        this.duration = duration;
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        this.isExecute = isExecute == undefined ? true : isExecute;

        this.beExecuted = null;

        this.canRemove = canRemove == undefined ? true : canRemove;
        this.callBack = callBack == undefined ? null : callBack;
    }
    /**
     * 
     * @param {*} target 
     * @param {*} needReport 
     * @param {*} reportLevel 
     * @param {number} reportStyle 122
     * @returns 
     */
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        //
        let refresh = false;
        let remain = [];
        const abort = target.SPECIAL_STATUS.some(block => {
            if (block.tactics.type == this.tactics.type && block.type == this.type) {
                if (block.damageValue >= this.damageValue) {
                    let r = new Report();
                    let text = ""
                    if (block.damageValue > this.damageValue) text = `${this.type}`
                    else text = `${this.type}`
                    r.InitExecuteReport(reportLevel, target, text);
                    return true;
                }
                else {
                    let r = new Report();
                    let text = `${this.type}`;
                    r.InitExecuteReport(reportLevel, target, text);
                    refresh = true;
                }
            }
            else remain.push(block);
        });
        if (abort) return false;
        target.SPECIAL_STATUS = remain;
        target.SPECIAL_STATUS.push(this);
        if (needReport && !refresh) {
            this.InitReport(reportStyle,reportLevel);
        }
        //
        target.FALL_INTO_CONTINUE_DAMAGE.forEach(block => {
            block.GetData(this.type, this.damageType, this.damageValue, this.applicator, this.tactics, this.duration, this);
            block.Execute();
        });
        return true;
    }
    /**
     * 
     * @param {*} reportStyle 
     * @param {*} reportLevel 1: Ax 2:AxBx 3: Axx 4: Axx 5: AxBx
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3,newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `${this.type}`;
        if(this.waitTime > 0) msg += "()";
        msg += "";
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`${this.tactics.name}`,`${this.type}`);
        if(reportStyle == 3){
            let r = new Report("");
            r.InitExecuteReport(reportLevel,this.beExecuted,`${this.tactics.name}${this.type}`);
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,`${this.tactics.name}${this.type}`);
        if(reportStyle == 5) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`${newTact.name}`,`${this.type}`);
    }
    //
    Execute(target, isByTrigger = false) {
        let rpLevel = 2;
        if (isByTrigger) rpLevel = 3
        Hurt(this.dmgId, this.applicator, target, this.damageValue, false, this.damageType, this.tactics, true, 3, this.type, rpLevel);
        if (this.callBack != null) {
            this.callBack();
        }
    }
}


export class CureStatusBlock {
    /**
     * 
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} type 
     * @param {*} cureMult 
     * @param {*} cureValue 
     * @param {*} duration 
     * @param {*} waitTime 
     * @param {*} rate 
     * @param {*} isExecute 
     * @param {boolean|true} canRemove true
     */
    constructor(applicator, tactics, type, cureMult, cureValue, duration, waitTime, rate, isExecute, canRemove) {
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //,
        this.type = type;
        //
        this.cureMult = cureMult;
        //
        this.cureValue = cureValue;
        //
        this.duration = duration;
        //
        this.waitTime = waitTime;
        //
        this.rate = rate;
        //
        this.isExecute = isExecute == undefined ? true : isExecute;
        this.canRemove = canRemove == undefined ? true : canRemove;
        this.beExecuted = null;
    }
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        let refresh = false;
        let remain = [];
        //
        for (let i in target.CURE_STATUS) {
            let block = target.CURE_STATUS[i];
            if (block.type == this.type && block.tactics.type == this.tactics.type) {
                if (block.cureMult >= this.cureMult) {
                    let r = new Report();
                    let text = block.cureMult > this.cureMult ? `${block.type}` : `${block.type}`;
                    r.InitExecuteReport(3, target, text);
                    return false;
                }
                else if(this.cureMult > block.cureMult){
                    let r = new Report();
                    r.InitExecuteReport(3, target, `${block.type}`);
                    refresh = true;
                }
                else remain.push(block);
            }
        }
        target.CURE_STATUS = remain;
        target.CURE_STATUS.push(this);
        //
        if (needReport && !refresh) {
            this.InitReport(reportStyle,reportLevel);
        }
        return true;
    }
    /**
     * 
     * @param {*} reportStyle 1: Ax 2:AxBx 3: Axx 4: Axx 5: AxBx
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3,newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `${this.type}`;
        if(this.waitTime > 0) msg += "()";
        msg += "";
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`${this.tactics.name}`,`${this.type}`);
        if(reportStyle == 3){
            let r = new Report("");
            r.InitExecuteReport(reportLevel,this.beExecuted,`${this.tactics.name}${this.type}`);
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,`${this.tactics.name}${this.type}`);
        if(reportStyle == 5) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`${newTact.name}`,`${this.type}`);
    }
    Execute(target, isByTrigger = false) {
        let realOdd = GetMoraleRate(target, this.rate);
        let rand = Math.floor(Math.random() * 100);
        if (this.rate >= 100 || rand < realOdd) {
            let reportL = 2;
            if (isByTrigger) reportL = 3;
            Cure(this.applicator, target, this.cureValue, this.tactics, true, null, 3, reportL, this.type);
        }
        else {
            let r = new Report();
            r.InitTwoTargetReport(2, this.applicator, target, `${this.tactics.name}${this.type}`, `${realOdd}%`);
        }

    }
}

