import Report from "../js/report";

import { GetAttr } from "../func/get";

export class AttrBlock {
    /**
     * 
     * @param {object} applicator 
     * @param {object} tactics 
     * @param {string} attrType 
     * @param {number} value 
     * @param {number} [duration=99] 
     * @param {boolean} isRepeat ,
     * @param {boolean} isBenefit 
     */
    constructor(applicator, tactics, attrType, value, duration, isRepeat, isBenefit) {
        this.applicator = applicator;
        this.tactics = tactics;
        this.attrType = attrType;
        this.value = value;
        this.duration = duration;
        this.isBenefit = isBenefit == undefined ? (this.value >= 0 ? true : false) : isBenefit;
        this.isRepeat = isRepeat == undefined ? false : isRepeat;
        //
        this.owner = null;
    }
    /**
     * 
     * @param {object} target 
     * @param {boolean} needReport 
     * @param {number} reportLevel 
     * @param {number} reportStyle 1Axx(x) 2AxBx(x) 3: Axx(x) 4: Axx(x) 5: AxBx(x)
     * @returns 
     */
    Init(target, needReport = true, reportLevel = 3,reportStyle=1) {
        //needReport
        this.owner = target;
        //
        let work = true;
        if (this.isRepeat) {
            target.ATTR.forEach(block=>{
                if (block.tactics == this.tactics && block.attrType == this.attrType) {
                    block.value += this.value;
                    block.duration = this.duration;//
                    if (needReport) {
                        let report = new Report();
                        let msg = `${this.attrType}`;
                        if (block.value > 0) {
                            msg += `${block.value}(${GetAttr(target, this.attrType)})`;
                        }
                        else msg += `${Math.abs(block.value)}(${GetAttr(target, this.attrType)})`;
                        report.InitExecuteReport(reportLevel, target, msg);
                    }
                    work = false;
                }
            })
        }
        let abort = false;
        if (work) {
            //
            let remain = [];
            abort = target.ATTR.some((block)=>{
                //
                if (block.attrType == this.attrType && this.tactics.type == block.tactics.type) {
                    //
                    if (this.isBenefit && block.isBenefit) {
                        //
                        if (this.value <= block.value) {
                            let report = new Report();
                            let str = `${block.attrType}`;
                            report.InitExecuteReport(reportLevel, target, str);
                            return true;
                        }
                        else {
                            let r = new Report();
                            r.InitExecuteReport(reportLevel, target, `${block.attrType}`)
                        }
                    }
                    //
                    else if (!this.isBenefit && !block.isBenefit) {
                        //
                        if (this.value >= block.value) {
                            let report = new Report();
                            let str = `${block.attrType}`;
                            report.InitExecuteReport(reportLevel, target, str);
                            return true;
                        }
                        else {
                            let r = new Report();
                            r.InitExecuteReport(reportLevel, target, `${block.attrType}`)
                        }
                    }
                    else remain.push(block);
                }
                else remain.push(block);
            })
            if(abort) return false;
            target.ATTR = remain;
            
            
            //
            target.BEFORE_APPLIED_ATTR_EXECUTE.forEach(block=>{
                if (block.duration > 0) {
                    if (block.isAppliedAd && this.attrType == ''
                        || block.isAppliedTact && this.attrType == ''
                        || block.isAppliedDe && this.attrType == ''
                        || block.isAppliedSpeed && this.attrType == ''
                    ) {
                        block.Execute(this);
                    }
                }
            })
            target.ATTR.push(this);
        }
        if (needReport && work) {
            this.InitReport(reportStyle,reportLevel)
        }
        //
        target.AFTER_APPLIED_ATTR_EXECUTE.forEach(block => {
            if (block.duration > 0) {
                if (block.isAppliedAd && this.attrType == ''
                    || block.isAppliedTact && this.attrType == ''
                    || block.isAppliedDe && this.attrType == ''
                    || block.isAppliedSpeed && this.attrType == ''
                ) {
                    block.Execute(this);
                }
            }
        });
        return true;
    }
    /**
     * 
     * @param {*} reportStyle 1Axx(x) 2AxBx(x) 3: Axx(x) 4: Axx(x) 5: AxBx(x)
     * @param {*} reportLevel 
     */
    InitReport(reportStyle=1,reportLevel=3,newApplicator = null, newTact = null){
        let report = new Report();
        let msg = `${this.attrType}`;
        msg += this.isBenefit ? "" : "";
        //00
        if(this.owner.driver.turn < 1 && this.value == 0){
            msg += ""
        }
        else msg += `${Math.abs(this.value)}(${GetAttr(this.owner, this.attrType)})`;
        
        if(reportStyle == 1 || reportStyle == null) report.InitExecuteReport(reportLevel, this.owner, msg);
        if(reportStyle == 2) report.InitTwoTargetReport(reportLevel,this.applicator,this.owner,`${this.tactics.name}`,msg);
        if(reportStyle == 3){
            let report = new Report("");
            report.InitExecuteReport(reportLevel, this.owner, msg + "");
        }
        if(reportStyle == 4) report.InitExecuteReport(reportLevel, this.owner, msg + "");
        if(reportStyle == 5) report.InitTwoTargetReport(reportLevel,newApplicator,this.owner,`${newTact.name}`,msg+"");
    }
}

export class AttrAppliedExecuteBlock{
    constructor(applicator,tactics,isBeforeApplied,isAppliedAd,isAppliedTact,isAppliedDe,isAppliedSpeed,execute,duration){
        this.applicator = applicator;
        this.tactics = tactics;
        this.isBeforeApplied = isBeforeApplied || true;
        this.isAfterApplied = !this.isBeforeApplied
        this.isAppliedAd = isAppliedAd || true;
        this.isAppliedTact = isAppliedTact || true;
        this.isAppliedDe = isAppliedDe || true;
        this.isAppliedSpeed = isAppliedSpeed || true;
        this.execute = execute;
        this.duration = duration;
        this.beExecuted = null;
        this.attrBlock = null;
    }
    Init(target){
        this.beExecuted = target
        if(this.isBeforeApplied) target.BEFORE_APPLIED_ATTR_EXECUTE.push(this);
        else target.AFTER_APPLIED_ATTR_EXECUTE.push(this);
    }
    Execute(attrBlock){
        this.attrBlock = attrBlock;
        this.execute();
    }
}