import Report from "../js/report";

class CounterAttackBlock{
    constructor(applicator,tactics,type,damageValue,rate,duration,waitTime){
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.type = type;
        //
        this.damageValue = damageValue;
        //
        this.rate = rate;
        //
        this.duration = duration;
        //
        this.waitTime = waitTime;
        this.beExecuted = null;
    }
    /**
     * 
     * @param {*} target 
     * @param {*} needReport 
     * @param {*} reportLevel 
     * @param {*} reportStyle 1:Ax 2:AxBx 3:Axx 4:Axx 5:AxBx
     * @returns 
     */
    Init(target,needReport = true,reportLevel = 3,reportStyle = 1){
        this.beExecuted = target;
        //
        for(let i in target.SPEC_ATTACK){
            if(target.SPEC_ATTACK[i].type == this.type){
                let r = new Report();
                r.InitExecuteReport(3,target,`${this.type}`);
                return false;
            }
        }
        target.SPEC_ATTACK.push(this);
        if(needReport){
            this.InitReport(reportStyle,reportLevel)
        }
        return true;
    }
    /**
     * 
     * @param {*} reportStyle 1:Ax 2:AxBx 3:Axx 4:Axx 5:AxBx
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle=1,reportLevel=3,newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `${this.type}`
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`${this.tactics.name}`,msg);
        if(reportStyle == 3) {
            let r = new Report("");
            r.InitExecuteReport(reportLevel,this.beExecuted,`${this.tactics.name}${this.type}`);
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,`${this.tactics.name}${this.type}`);
        if(reportStyle == 4 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`${newTact.name}`,`${this.type}`);
    }
    SelfDestroy(){
        for(let i in this.beExecuted.SPEC_ATTACK){
            if(this.beExecuted.SPEC_ATTACK[i] == this){
                this.beExecuted.SPEC_ATTACK.splice(i,1);
            }
        }
    }
}

export default CounterAttackBlock;