import Report from "../js/report";

import { LaunchAttack,LaunchActiveTactics } from "../func/action";
import { IsGetStatus } from "../func/judger";
import { ConsBlockDuration, ConsCureStatusBlock, ConsDamageStatusBlock, ConsExecuteBlock } from "../func/handler";

//
class Warrior{
    constructor(data,position,belong,global,driver){
        this.driver = driver;
        this.global = global;
        this.belong = belong;
        this.position = position;
        this.red = data.red;
        this.sign = data.sign;
        //id
        this.id = data.id;
        //
        this.alive = true;
        //
        this.ally = null;
        //
        this.enemy = null;
        //
        this.action = true;
        this.actionStage = 1, // 123
        //
        this.level = data.level;
        //
        this.gender = data.gender;
        //
        this.name = data.name;
        //
        this.camp = data.camp;
        //
        this.arms = data.arms;
        //
        this.unitType = "";
        //
        this.cost = data.cost;
        //
        this.ackRange = data.range;
        //
        this.hp = data.level * 100;
        this.hpMax = data.level * 100;
        this.wound = 0;
        //
        this.artifact = data.artifact;
        //
        this.morale = 100;
        //
        this.armsSpec = data.armsSpec
        this.armsCommonSpec = data.armsCommonSpec
        this.upArms = data.upArms;
        this.choseUpArmsIdx = data.choseUpArmsIdx;
        this.choseArmsCharacterIdx = data.choseArmsCharacterIdx
        //
        this.attr = {
            ad : Math.round(data.attr.ad + data.grow.ad * (data.level - 1) + data.plus.ad),
            de : Math.round(data.attr.de + data.grow.de * (data.level - 1) + data.plus.de),
            tact : Math.round(data.attr.tact + data.grow.tact * (data.level - 1) + data.plus.tact),
            speed : Math.round(data.attr.speed + data.grow.speed * (data.level - 1) + data.plus.speed),
        }
        //
        this.img = {
            body : data.img.body,
            preview : data.img.preview,
            head : data.img.head,
            banner : data.img.banner,
        }
        //
        this.TacticsArr = [];
        //
        this.WaitTacticsArr = [];

        this.reportWait = [];

        //
        this.ATTR = [];
        //
        this.IGNORE = [];

        //()
        this.STATUS = [];
        //...
        this.SPECIAL_STATUS = [];
        //
        this.CURE_STATUS = [];
        //
        this.SPEC_ATTACK = [];

        //
        this.STATUS_RESIST = [];

        //
        this.RANGE = [];
        //
        this.RATE_VAR = [];


        //
        this.DAMAGE_VAR = [];
        //
        this.CURE_VAR = [];
        //
        this.STATUS_DAMAGE_VAR = []

        
        
        
        
        
        //
        this.DODGE = [];
        //
        this.EXECUTE = [];
        //
        this.BEFORE_MAKE_DAMAGE = [];
        //
        this.BEFORE_GET_HURT = [];
        //
        this.MAKE_DAMAGE = [];
        //
        this.AFTER_DAMAGE = [];
        //
        this.AFTER_CONTINUE_DAMAGE = [];
        //
        this.GET_CURE = [];
        //
        this.ACTIVE_TACTICS_EXECUTE = [];
        //
        this.LAUNCH_ATTACK_EXECUTE = [];
        //
        this.LAUNCH_COMBO_EXECUTE = [];
        
        //* 
        this.DISCOUNT = [];

        //
        this.FALL_INTO_CONTINUE_DAMAGE = [];
        //
        this.BEFORE_FALL_INTO_STATUS = [];
        this.FALL_INTO_STATUS = [];

        //
        this.BEFORE_APPLIED_ATTR_EXECUTE = [];
        //
        this.AFTER_APPLIED_ATTR_EXECUTE = [];
        

        //
        this.BEFORE_TARGET_SELECT_EXECUTE = []
        this.AFTER_TARGET_SELECT_EXECUTE = [];

        //
        this.STATISTICS = {
            atk : 0,//
            tact : 0,//
            cure : 0,//
        }
        //  app  
        for (const key in data) {
            if (!this.hasOwnProperty(key)) {
                this[key] = data[key];
            }
        }
    }
    //   
    Execute(){
        this.actionStage = 1;

        let report = new Report();
        report.InitExecuteReport(1,this,` ${this.hp}`,true,'');
        // report.InitSysMsg(this.global.ReportHeap,"",` (${this.hp})`)
        //------------------------------------------------------------------
        ConsExecuteBlock(this,"");//
        
        //
        ConsBlockDuration(this);

        ConsExecuteBlock(this,"");//

        this.actionStage = 2;
        if(!this.alive){
            //
            this.action = false;
            return;
        }
        //
        let insight = IsGetStatus(this,"");
        //
        if(IsGetStatus(this,"")){
            let r = new Report();
            if(insight) r.InitExecuteReport(2,this,``);
            else r.InitExecuteReport(2,this,``);
        }
        
        //
        if(IsGetStatus(this,"") && !insight){
            let r = new Report();
            r.InitExecuteReport(2,this,"");
        }
        else{
            if(IsGetStatus(this,"") && insight){
                let r = new Report();
                r.InitExecuteReport(2,this,``);
            }
            //
            if(IsGetStatus(this,"") && !insight){
                let r = new Report();
                r.InitExecuteReport(2,this,"");
            }
            else if(IsGetStatus(this,"")){
                let r = new Report();
                r.InitExecuteReport(2,this,"");
            }
            else{
                if(IsGetStatus(this,"") && insight){
                    let r = new Report();
                    r.InitExecuteReport(2,this,``);
                }
                LaunchActiveTactics(this);
            } 
            //
            if(IsGetStatus(this,"")){
                let report = new Report();
                report.InitExecuteReport(2,this,"");
            }
            else if(IsGetStatus(this,"") && !insight && !IsGetStatus(this,"")){
                let report = new Report();
                report.InitExecuteReport(2,this,"");
            }
            else{
                if(IsGetStatus(this,"")){
                    if(insight){
                        let r = new Report();
                        r.InitExecuteReport(2,this,``);
                    }
                    else if(IsGetStatus(this,"")){
                        let r = new Report();
                        r.InitExecuteReport(2,this,``);
                    }
                }
                LaunchAttack(this);
                if(IsGetStatus(this,"")){
                    let report = new Report();
                    report.InitExecuteReport(2,this,"");
                    LaunchAttack(this);
                }
            }
        }
        this.actionStage = 3;
        //
        ConsExecuteBlock(this,"");//
        this.action = false;
        //
        this.wound = Math.floor(this.wound * 0.87);
        this.actionStage = 4;
    }
}

export default Warrior;