        wait: 0,
        execute(self) {
            let that = this;

            let value = GetAdditionValue(self, 80, '', 0.325); //
            let decValue = GetAdditionValue(self, 20, '', 0.16); //
            let declineIndex = 4 //
            let attrDeclineValue = Math.floor(value / declineIndex);

            let tag = '' + self.belong + self.position;
            const attrTypes = ['', ""];
            self.ally.forEach(tgt => {
                attrTypes.forEach(key => {
                    let block = new AttrBlock(self, this, key, 0, 99, true, true);
                    block.Init(tgt, false);
                    block[tag] = 0;
                    block.InitReport(1, 2);
                })
                let block = new DamageVarBlock(self, this, '', false, 0, false, false, 99, 0,true);
                let work = block.Init(tgt, null, false);
                if (work) {
                    block.InitReport(1, 2);
                }
            })
            let action = function () {
                if (self.driver.turn == 2 || self.driver.turn == 4 || self.driver.turn == 6) {
                    let r = new Report();
                    r.InitExecuteReport(2, self, ``);
                    self.ally.forEach(tgt => {
                        ClearBlockByTact(tgt, "ATTR", that);
                        ClearBlockByTact(tgt, "DAMAGE_VAR", that);
                        //
                        attrTypes.forEach(key => {
                            let block = new AttrBlock(self, that, key, value, 99, true, true);
                            block.Init(tgt, true, 3, 1);
                            block[tag] = declineIndex;
                        })
                    })
                    self.ally.forEach(tgt => {
                        let block = new DamageVarBlock(self, that, '', false, -decValue, false, false, 99, 0,true);
                        block.Init(tgt, null, true, 3, 2);
                        block[tag] = declineIndex
                    })
                }
            }
            let block = new ExecuteBlock(self, this, '', action, 99);
            block.Init(self, false);
            let makeDamage = function () {
                //
                let target = this.beExecuted;
                for (let j in target.ATTR) {
                    if (target.ATTR[j].tactics == that && target.ATTR[j][tag] > 0) {
                        target.ATTR[j].value -= attrDeclineValue;
                        target.ATTR[j][tag]--;
                        target.ATTR[j].InitReport(2, 3);
                    }
                }
                for (let j in target.DAMAGE_VAR) {
                    if (target.DAMAGE_VAR[j].tactics == that && target.DAMAGE_VAR[j][tag] > 0) {
                        target.DAMAGE_VAR[j].value += Math.floor(decValue / declineIndex);
                        target.DAMAGE_VAR[j][tag]--;
                        target.DAMAGE_VAR[j].InitReport(2, 3);
                    }
                }
            }
            for (let i in self.ally) {
                let target = self.ally[i];
                let block = new AfterMakeDamageBlock(self, this, makeDamage, 99);
                block.Init(target, this.name);
            }
            let r = new Report();
            r.InitExecuteReport(2, self, ``);
            this.STATISTICS.times += 1;
        }
