        targetNumber: 3,
        arms: [''],
        wait: 1,
        execute(self) {
            let that = this;
            let dmgMult = GetAdditionValue(self, 111, '', 0.95);
            let fireValue = GetAdditionValue(self, 221, '', 2.45)
            let realRange = GetRange(self, this);
            let realTargetNumber = this.targetNumber
            let res = GetTarget(self, realRange, false, true, realTargetNumber, false, true, this, "");
            let tag = "trigger" + self.position + self.belong;
            let lock = "lock" + self.position + self.belong;
            let getHurt = function () {
                let tgt = this.beExecuted
                if (this.damageFromTactics == that && this.statusDmgType == '' || tgt[lock]) return;
                if (!tgt[tag]) {
                    tgt[lock] = true;
                    TriggerDmgStatus(tgt, that, "");
                    ClearSpecialStatus(self, tgt, "", that, true, 3, 3, that);
                    tgt[tag] = true;
                }
                tgt[lock] = false;
            }
            res.forEach(tgt => {
                let [damage, dmgId] = CountDamage(self, tgt, dmgMult, "", false, this, null);
                Hurt(dmgId, self, tgt, damage, false, "", this, true, 1, null, 3);
            })
            res.forEach(tgt => {
                let [damage, dmgId] = CountDamage(self, tgt, dmgMult, "", false, this, "");
                let statusblock = new DamageStatusBlock(dmgId, self, this, '', fireValue, damage, '', 99, 0, false);
                let work = statusblock.Init(tgt, true, 3, 1);
                if (work) {
                    ClearBlockByTact(tgt, 'AFTER_DAMAGE', this);
                    let block = new AfterGetDamageBlock(self, this, getHurt, 99, true, true, true, true, true, true, true);
                    block.Init(tgt, "");
                    tgt[tag] = false;
                }
            })
            this.STATISTICS.times += 1;
        }
