        execute(self) {
            let that = this;
            //
            let dmgMult = 125;
            //
            let value = GetAdditionValue(self, 24, '', 0.128);
            let triggerTime = 2;
            let tag = '' + self.belong + self.position
            let realRange = GetRange(self, this);
            let res = GetTarget(self, realRange, false, true, 2, false, true, this);
            if (self.name == "") {
                let block = new StatusBlock(self, this, '', 1, 0);
                block.Init(self, true, 3, 1);
            }
            let trigger = function () {
                let target = this.beExecuted;
                if (target[tag] > 0) {
                    TriggerDmgStatus(target, that, "");
                    target[tag] -= 1;
                    let r = new Report("");
                    r.InitExecuteReport(3, self, `${target[tag]}`);
                }
            }
            res.forEach(tgt => {
                let [damage, dmgId] = CountDamage(self, tgt, dmgMult, "", false, this, "");
                let block = new DamageStatusBlock(dmgId, self, this, "", dmgMult, damage, "", 2, 0, false, true);
                let work = block.Init(tgt, true, 3, 1);
                if (work) {
                    ClearBlockByTact(tgt, "AFTER_DAMAGE", this);
                    ClearBlockByTact(tgt, "LAUNCH_ATTACK_EXECUTE", this);
                    let atkblock = new AttackExecuteBlock(self, this, trigger, 2, 0, true, "");
                    atkblock.Init(tgt, false);
                    let hurtBlock = new AfterGetDamageBlock(self, this, trigger, 2, true, false, true, false, false, false, false);
                    hurtBlock.Init(tgt);
                    tgt[tag] = triggerTime;
                }
            })
            //
            res.forEach(tgt => {
                let block = new DamageVarBlock(self, this, '', false, value, false, false, 2, 0);
                block.Init(tgt, null, true, 3, 1);
            })
            if (self.name == "") {
                ClearStatusBlock(self, self, "", true, this, true, 3, 3, this);
            }
            this.STATISTICS.times += 1;
        }
