import { IsDodge, IsGetStatus } from "./judger";
import Report from "../js/report";
import { ConsDodgeBlock, ConsOnceDamageBlock, HandlerOnceDamageVarReport } from "./handler";


/**
 * 
 * @param {string} dmgId - damage
 * @param {Object} attacker - 
 * @param {Object} receiver - 
 * @param {number} orgDmgVal - 
 * @param {boolean} isComAtk - 
 * @param {string} damageType - 
 * @param {Object|null} tacticsItem - 
 * @param {boolean} [needReport=true] - 
 * @param {Object|null|number} [reportStyle=1] - 1x...2xxx...3xxx... 4...5...1
 * @param {string|null} [statusDmgType=null] - null
 * @param {number} [reportLevel=3] 3
 * @returns {Object} ,returnMsg = {
        realDmg : damage,
        status : true,
    }
*/
export const Hurt = (dmgId,attacker, receiver, orgDmgVal, isComAtk, damageType, tacticsItem, needReport = true, reportStyle = 1, statusDmgType = null,reportLevel = 3) => {
    let damage = orgDmgVal;
    for(let i in receiver.BEFORE_GET_HURT){
        receiver.BEFORE_GET_HURT[i].Execute(dmgId,attacker, receiver, orgDmgVal,isComAtk,damageType,tacticsItem,statusDmgType);
        damage = receiver.BEFORE_GET_HURT[i].damageValue;
    }
    
    //
    if(damage > receiver.hp) damage = receiver.hp;
    receiver.hp -= damage;
    //
    receiver.wound += Math.floor(damage * 0.95);
    if (receiver.hp <= 0) {
        receiver.hp = 0;
        receiver.wound = Math.floor(receiver.wound * 0.6)
        receiver.alive = false;
    }
    //
    if (needReport) {
        let report = new Report();
        if(reportStyle == 1 || reportStyle == null) report.InitExecuteReport(reportLevel, receiver, `${damage}(${receiver.hp})`);
        if(reportStyle == 2){
            if(tacticsItem) report.InitTwoTargetReport(reportLevel,attacker,receiver,`${tacticsItem.name}`,`${damage}(${receiver.hp})`)
            else report.InitTwoTargetReport(reportLevel,attacker,receiver,``,`${damage}(${receiver.hp})`)
        }
        if(reportStyle == 3){
            if(statusDmgType){
                report.InitTwoTargetReport(reportLevel,receiver,attacker,``,`${tacticsItem.name}${statusDmgType}${damage}(${receiver.hp})`)
            }
        }
        if(reportStyle == 4){
            report.InitExecuteReport(reportLevel,receiver,`${damage}(${receiver.hp})`)
        }
        if(reportStyle == 5){
            report.InitExecuteReport(reportLevel,receiver,`${damage}(${receiver.hp})`)
        }
    }

    //
    HandlerOnceDamageVarReport(attacker,dmgId);
    HandlerOnceDamageVarReport(receiver,dmgId);

    //
    if (isComAtk || tacticsItem == null) attacker.STATISTICS.atk += damage;
    else {
        tacticsItem.STATISTICS.damage += damage;
    }
    //
    if (attacker.alive && damage != 0) {
        let len = attacker.MAKE_DAMAGE.length;
        for (let i = 0; i < len; i++) {
            if (!attacker.MAKE_DAMAGE[i]['action']) {
                attacker.MAKE_DAMAGE[i].GetDamageData(damage, receiver, tacticsItem, isComAtk, damageType,statusDmgType)
                attacker.MAKE_DAMAGE[i].Execute();
                attacker.MAKE_DAMAGE[i]['action'] = true;
            }
            if (len != attacker.MAKE_DAMAGE.length) {
                i = 0;
                len = attacker.MAKE_DAMAGE.length;
            }
        }
        //action
        for (let i in attacker.MAKE_DAMAGE) {
            attacker.MAKE_DAMAGE[i]['action'] = false;
        }
    }
    //
    if (receiver.alive && damage != 0) {
        let length = receiver.AFTER_DAMAGE.length;
        for (let i = 0; i < length; i++) {
            if (isComAtk) {
                if (receiver.AFTER_DAMAGE[i].getAdDmgExecute || receiver.AFTER_DAMAGE[i].getCAttackExecute) {
                    receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isComAtk, damageType,statusDmgType)
                    receiver.AFTER_DAMAGE[i].Execute();
                }
            }
            else {
                if (tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.command
                    || tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.active
                    || tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.combo
                    || tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.passive) {

                    if (receiver.AFTER_DAMAGE[i].getAdDmgExecute && !receiver.AFTER_DAMAGE[i].getTactDmgExecute) {
                        if (damageType == '') {
                            receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isComAtk, damageType,statusDmgType)
                            receiver.AFTER_DAMAGE[i].Execute();//receiver.AFTER_DAMAGE
                        }
                    }
                    else {
                        receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isComAtk, damageType,statusDmgType)
                        receiver.AFTER_DAMAGE[i].Execute();//receiver.AFTER_DAMAGE
                    }

                }
            }
            if (receiver.AFTER_DAMAGE.length != length) {
                i--;
                length = receiver.AFTER_DAMAGE.length;
            }
        }
    }
    //xp
    if (receiver.alive && damage != 0 && statusDmgType) {
        let length = receiver.AFTER_CONTINUE_DAMAGE.length;
        for (let i = 0; i < length; i++) {
            if (tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.command
                || tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.active
                || tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.combo
                || tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.passive) {
                receiver.AFTER_CONTINUE_DAMAGE[i].GetData(damage, attacker, tacticsItem, damageType);
                receiver.AFTER_CONTINUE_DAMAGE[i].Execute();//receiver.AFTER_DAMAGE
            }
            if (receiver.AFTER_CONTINUE_DAMAGE.length != length) {
                i--;
                length = receiver.AFTER_CONTINUE_DAMAGE.length;
            }
        }
    }
    let returnMsg = {
        realDmg : damage,
        status : true,
    }
    return returnMsg;
}


/**
 * 
 * @param {Object} initiator - 
 * @param {Object} receiver - 
 * @param {number} cureNumber - 
 * @param {Object} [cureTactics=null] - 
 * @param {boolean} [needReport=true] - 
 * @param {Object|null} [reportParma=null] - 
 * @param {number} [reportStyle=1] - 1x...2xxx...3xxx...1
 * @param {number} [reportLevel=3] - 
 * @returns {Object} 
*/
export const Cure = (initiator, receiver, cureNumber, cureTactics = null, needReport = true, reportParma = null, reportStyle = 1, reportLevel = 3,statusType=null) => {
    //
    let cure = cureNumber > 0 ? cureNumber : -cureNumber;
    if (IsGetStatus(receiver, "")) {
        let report = new Report();
        report.InitExecuteReport(3, receiver, ``);
        return false;
    }

    //
    if(cure > receiver.wound) cure = receiver.wound;
    receiver.hp += cure;
    receiver.wound -= cure;
    //
    cureTactics.STATISTICS.cure += cure;
    if (needReport) {
        if (reportParma == null) {
            let report = new Report();
            if (reportStyle == 1) report.InitExecuteReport(reportLevel, receiver, `${cure}(${receiver.hp})`);
            if (reportStyle == 2) {
                if (cureTactics) report.InitTwoTargetReport(reportLevel, initiator, receiver, `${cureTactics.name}`, `${cure}(${receiver.hp})`);
                else report.InitTwoTargetReport(reportLevel, initiator, receiver, ``, `${cure}(${receiver.hp})`);
            }
            if(reportStyle == 3){
                if (cureTactics){
                    report.InitTwoTargetReport(reportLevel, initiator,receiver,`${cureTactics.name}${statusType}`,`${cure}(${receiver.hp})`)
                }
                else report.InitExecuteReport(reportLevel, receiver, `${statusType}${cure}(${receiver.hp})`);
            }

        }
        else if (reportParma != null) {
            let r = new Report();
            r.PutReportToHeap(receiver.global.ReportHeap, reportParma);
        }
    }
    //
    receiver.GET_CURE.forEach(block => {
        block.Execute(cureTactics, initiator, cureNumber);
    });
    return true;
}

