import Report from "../js/report";

class DodgeBlock{
    constructor(applicator,tactics,duration,rate){
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.duration = duration;
        //
        this.rate = rate == undefined ? 120 : rate;
    }
    Init(target,needReport = true,reportLevel = 3,reportStyle = 1){
        const abort = target.DODGE.some(block=>{
            //
            if(block.tactics.type == this.tactics.type && block.tactics != this.tactics){
                let report = new Report();
                report.InitExecuteReport(reportLevel,target,``);
                return true;
            }
        })
        //
        let remain = [];
        target.DODGE.forEach(block => {
            if(block.tactics == this.tactics) remain.push(block);
        });
        target.DODGE = remain;
        if(abort) return false;
        target.DODGE.push(this);
        if(needReport){
            let msg = ``
            if(this.rate >= 100) msg = `()`
            let r = new Report();
            if(reportStyle == 1) r.InitExecuteReport(reportLevel,target,msg);
            if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,target,`${this.tactics.name}`,`${this.rate >= 100?"":"()"}`);
        }
        return true;
    }
    InitReport(){
        
    }
}

export default DodgeBlock;